}

.view {
  background-color: #11c2d9;
}

.card {
  border-style: solid;
  border-width: 1px;
  border-color: #15aa41;
  margin-bottom: 330rem;
}
.navbar {
  background-color: #09af99
}

a {
  color: #FFFFFF;
  font-size: 14pt;
}

a:hover {
  color: rgb(160, 157, 157)
}

.jumbotron {
  background-image: url(/static/media/books.2ea5be2e.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  height: 300px; 
  width: 100%;
  padding-top: 10; 
  text-align: center
}
.page-footer {
  background-color: #09af99;
  margin-top: 10rem;
  padding-top: 2%;
  color: #FFFFFF;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  text-align: center;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

